.switch {
  font-size: 1vw;
  position: relative;
  display: inline-block;
  width: 3.4em;
  height: 1.9em;
}

/* Hide default HTML checkbox */
.switch input, .switch .contact textarea, .contact .switch textarea, .switch .dashboard .dashboard__main > div:nth-child(2) form textarea, .dashboard .dashboard__main > div:nth-child(2) form .switch textarea {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 1.5em;
  width: 1.5em;
  left: 0.2em;
  bottom: 0.2em;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider, .contact textarea:checked + .slider, .dashboard .dashboard__main > div:nth-child(2) form textarea:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider, .contact textarea:focus + .slider, .dashboard .dashboard__main > div:nth-child(2) form textarea:focus + .slider {
  box-shadow: 0 0 0.1em #2196F3;
}

input:checked + .slider:before, .contact textarea:checked + .slider:before, .dashboard .dashboard__main > div:nth-child(2) form textarea:checked + .slider:before {
  transform: translateX(1.5em);
}

/* Rounded sliders */
.slider.round {
  border-radius: 1em;
}

.slider.round:before {
  border-radius: 50%;
}

